"use client"
import { Alert, Box, Button, Checkbox, Divider, Stack, TextField, Typography } from '@mui/material'
import Image from 'next/image'
import { useRouter } from 'next/navigation'
import { useState } from 'react'
import axios from 'axios'
import { API_ROUTER } from '@/lib/api/router'

export default function RegisterPage() {
    const router = useRouter()
    const [username, setUsername] = useState('')
    const [password, setPassword] = useState('')
    const [confirmPassword, setConfirmPassword] = useState('')
    const [email, setEmail] = useState('')
    const [loading, setLoading] = useState(false)

    const exceptions = () => {
        if (!username) {
            return <Alert severity="error">Username is required</Alert>
        }
        if (!password) {
            return <Alert severity="error">Password is required</Alert>
        }
        if (!confirmPassword) {
            return <Alert severity="error">Confirm Password is required</Alert>
        }
        if (!email) {
            return <Alert severity="error">Email is required</Alert>
        }
        if (password !== confirmPassword) {
            return <Alert severity="error">Password and Confirm Password do not match</Alert>
        }

        if (password.length < 8) {
            return <Alert severity="error">Password must be at least 8 characters</Alert>
        }
        return null
    }

    const handleSubmit = async () => {
        if (!exceptions()) {
            return
        }
        try {
            setLoading(true)
            const UserData = {
                username,
                password,
                email
            }

            const res = await axios.post(`${API_ROUTER}/users`, UserData)

            if (res.status === 201) {
                <Alert severity="success">Register successfully</Alert>
                router.push("/login")
            } else {
                <Alert severity="error">Register failed</Alert>
            }

        } catch (error) {
            <Alert severity="error">Register failed</Alert>
        } finally {
            setLoading(false)
        }
    }

    return (
        <Box display={"flex"} justifyContent={"space-between"} alignItems={"center"} height={'100vh'} width={"100%"}>
            <Box sx={{ width: "100%", height: "100%", backgroundColor: "#fff" }}>
                <Image src={'/banner/Banner1.jpg'} width={1920} height={1080} style={{ width: "100%", height: "100%", objectFit: "cover" }} alt="Banner" />
            </Box>
            <Box sx={{ width: "100%", height: "100%", display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "center" }}>
                <Stack spacing={1} alignContent={"flex-start"} width={"50%"} mb={2}>
                    <Typography sx={{ fontSize: "44px", fontWeight: "600" }}>
                        VIVEX ID
                    </Typography>
                    <Typography sx={{ fontSize: "20px", fontWeight: "400" }}>Register to your account</Typography>
                </Stack>
                <Stack direction={"column"} spacing={2} width={"50%"}>
                    <Stack direction={"column"} spacing={1} width={"100%"}>
                        <TextField fullWidth placeholder='Email' onChange={(e) => setEmail(e.target.value)} />
                    </Stack>
                    <Stack direction={"column"} spacing={1} width={"100%"}>
                        <TextField fullWidth placeholder='Username' onChange={(e) => setUsername(e.target.value)} />
                    </Stack>
                    <Stack direction={"column"} spacing={1} width={"100%"}>
                        <TextField fullWidth placeholder='Password' type='password' onChange={(e) => setPassword(e.target.value)} />
                    </Stack>
                    <Stack direction={"column"} spacing={1} width={"100%"}>
                        <TextField fullWidth placeholder='Confirm Password' type='password' onChange={(e) => setConfirmPassword(e.target.value)} />
                    </Stack>
                </Stack>
                <Stack direction={"row"} spacing={1} alignItems={"center"} justifyContent={"flex-start"} width={"52%"}>
                    <Checkbox onChange={(e) => setLoading(e.target.checked)} />
                    <Typography sx={{ fontSize: "14px", fontWeight: "400" }}>I agree to the terms and conditions</Typography>
                </Stack>
                {/* <Stack direction={"row"} justifyContent={"flex-end"} spacing={2} width={"50%"} mt={2} mb={2}>
                    <Box component={"button"} sx={{ fontSize: "14px", fontWeight: "600" }}>
                        Create Account
                    </Box>
                    <Divider orientation="vertical" flexItem />
                    <Box component={"button"} sx={{ fontSize: "14px", fontWeight: "600" }}>
                        Forgot Password?
                    </Box>
                </Stack> */}
                <Button sx={{ width: "50%", height: "50px", border: "1px solid #fff" }} onClick={handleSubmit}>
                    <Typography sx={{ fontSize: "14px", fontWeight: "600" }}>Register</Typography>
                </Button>
            </Box>
        </Box>
    )
}
